.article {
  padding-top: 20px;
  padding-bottom: 35px;

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 22px;
    padding-bottom: 36px;
  }
}

.article__top-wrap {
  display: flex;
  justify-content: space-between;
  margin-bottom: 26px;
}

.article__title {
  @include title-section;
}

// =================
//  CONTROL BUTTONS
// =================

.article__control {

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

// ======================
//  ARTICLE ITEMS SWIPER
// ======================

.article-list {

  // --- max-width: 576px ---
  @include mobile {
    flex-direction: column;
    gap: 24px;
  }
}

.article-list__item {
  height: auto;
}

// --------------------
// --- ARTICLE CARD ---
// --------------------

.article-card {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100%;
  min-height: 676px;
  background-color: $grey-background-color;
  border-radius: 10px;

  // --- max-width: 1024px ---
  @include tablet {
    min-height: 456px;
  }
}

.article-card__wrap-text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  flex-grow: 1;
  gap: 15px;
  height: 100%;
  padding: 30px;
}

.article-card__descr {
  color: $text-color;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.4;

  // --- max-width: 1024px ---
  @include tablet {
    font-size: 16px;
  }
}
