.content-card__descr-block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  height: 100%;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 1 / span 5;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    gap: 32px;
    padding-bottom: 28px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    gap: 29px;
    padding-bottom: 31px;
  }

  // --- max-width: 576px ---
  @include mobile {
    gap: 31px;
    padding-bottom: 33px;
  }
}

.content-card__txt-wrap {

  // --- max-width: 930px ---
  @include between-tablet {
    max-width: 515px;
  }
}

.content-card__descr-title {
  margin-bottom: 8px;
  color: $text-color;
  font-size: 16px;
  font-weight: 600;
  line-height: 100%;
}

.content-card__descr {
  color: $text-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 180%;
}

.content-card__descr:not(:last-child) {
  margin-bottom: 7px;
}

.content-card__pattern {
  display: flex;
  gap: 31px;

  // --- max-width: 930px ---
  @include between-tablet {
    gap: 18px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    gap: 23px;
  }

  // --- max-width: 576px ---
  @include mobile {
    gap: 16px;
  }
}

// ---------------------------------
// --- TECHNICAL CHARACTERISTICS ---
// ---------------------------------

.characteristics {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

.content-card__characteristics {

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 6 / span 4;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 1;
    grid-row: 5 / 6;
    padding-top: 29px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 31px;
  }
}

.characteristics__row {

  // --- max-width: 576px ---
  @include mobile {
    display: flex;
    flex-direction: column;
    gap: 2px;
  }
}

.characteristics__row:not(:last-child) {

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 11px;
  }
}

.characteristics__parameter {
  width: 52%;
  text-align: left;
  vertical-align: top;

  // --- max-width: 1024px ---
  @include tablet {
    width: 46%;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    width: 65%;
  }

  // --- max-width: 576px ---
  @include mobile {
    width: 100%;
    padding: 0;
  }
}

.characteristics__value {

  // --- max-width: 576px ---
  @include mobile {
    padding: 0;
  }
}

.characteristics__txt {
  font-size: 14px;
  font-weight: 400;
  line-height: 164%;

  // --- max-width: 576px ---
  @include mobile {
    line-height: 141%;
  }
}

.characteristics__txt--par {
  position: relative;
  display: inline-block;
  width: 100%;
  color: $grey-color;
  vertical-align: top;
  overflow: hidden;
  letter-spacing: normal;

  // --- max-width: 576px ---
  @include mobile {
    position: unset;
    display: block;
    width: 100%;
    vertical-align: unset;
    font-size: 10px;
    letter-spacing: unset;
    overflow: unset;
  }
}

.characteristics__txt-head {
  position: relative;
  z-index: 1;
  padding-right: 4px;
  background-color: $white-color;

  // --- max-width: 576px ---
  @include mobile {
    position: unset;
    padding-right: 0;
  }
}

.characteristics__txt--value {
  color: $text-color;
  letter-spacing: normal;
  vertical-align: bottom;

  // --- max-width: 576px ---
  @include mobile {
    display: block;
    padding-left: 0;
    line-height: 99%;
  }
}

// ---------------------
// --- DIVIDING LINE ---
// ---------------------

.characteristics__txt--par::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 7px;
  display: block;
  width: 100%;
  height: 20px;
  border-bottom: 1px dashed $grey-color;

  // --- max-width: 576px ---
  @include mobile {
    content: unset;
  }
}
