.header__form {
  position: relative;
  max-width: 1061px;
  width: 100%;

  // --- max-width: 1024px ---
  @include tablet {
    max-width: 733px;
  }
}

.search {
  position: relative;
  width: 100%;
}

// --------------------------
// --- SEARCH INPUT FIELD ---
// --------------------------

.search__input {
  width: 100%;
  padding: 20px 57px;
  color: $text-color;
  border: 2px solid $grey-background-color;
  border-radius: 10px;
  background-color: $grey-background-color;
  transition: background-color 0.4s ease-in-out,
    color 0.4s ease-in-out,
    border-color 0.4s ease-in-out;

  // --- max-width: 1024px ---
  @include tablet {
    padding: 19px 57px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding: 20px 58px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 18px 57px;
  }
}

.search__input::placeholder {
  color: $grey-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 120%;
}

// ---------------------
// --- SEARCH BUTTON ---
// ---------------------

.search__btn {
  position: absolute;
  top: 21px;
  left: 32px;
  font-size: 0;
}

.search__icon {
  width: 20px;
  height: 18px;
  fill: $grey-color;
  transition: fill 0.4s ease-in-out;
}

// --- searching form transition effects ---

.search__input:focus-visible {
  border-color: $primary-color;
  background-color: #f8efff;
}

@media (any-hover: hover) {
  .search__input:hover {
    border-color: $disabled-color;
    background-color: $disabled-color;
  }
}

.search__input:active {
  border-color: $grey-background-color;
  background-color: $grey-background-color;
}

.search__input:not(:placeholder-shown) {
  border-color: $grey-background-color;
  background-color: $grey-background-color;
}

// -------------------------------
// --- TO OPEN DROPDOWN BUTTON ---
// -------------------------------

.header__dropdown-btn {
  position: absolute;
  right: 32px;
  bottom: 15px;
  width: 32px;
  height: 32px;
  transition: visibility 0.3s ease,
    opacity 0.3s ease,
    transform 0.3s ease;

  // --- max-width: 1024px ---
  @include tablet {
    bottom: 14px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    right: 31px;
    bottom: 16px;
  }

  // --- max-width: 576px ---
  @include mobile {
    bottom: 13px;
  }
}

.header__dropdown-icon {
  width: 32px;
  height: 32px;
  fill: transparent;
  cursor: pointer;
  transition: stroke 0.4s ease-in-out;
}

.header__dropdown-icon circle {
  stroke: $primary-color;
  transition: stroke 0.4s ease-in-out;
}

.header__dropdown-icon path {
  stroke: $primary-color;
  transition: stroke 0.4s ease-in-out;
}

// --- button transitions effects ---

.header__dropdown-btn:focus-visible circle,
.header__dropdown-btn:focus-visible path {
  stroke: $primary-light-color;
}

@media (any-hover: hover) {
  .header__dropdown-btn:hover circle,
  .header__dropdown-btn:hover path {
    stroke: $primary-light-color;
  }
}

.header__dropdown-btn:active circle,
.header__dropdown-btn:active path {
  stroke: $primary-dark-color;
}

// --- active button ---

.dropdown-btn-hide {
  visibility: hidden;
  opacity: 0;
  transform: scale(0.4);
}

// ---------------------------
// --- DROPDOWN LIST BLOCK ---
// ---------------------------

.dropdown {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
  max-width: 300px;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  transform: scale(0.7);
  transition: visibility 0.7s ease,
    opacity 0.7s ease,
    transform 0.5s ease;
}

.dropdown__inner {
  position: relative;
}

.choices__inner {
  cursor: pointer;
}

.choices.is-focused {
  outline: none;
  -webkit-appearance: none;
  appearance: none;
}

// --------------------
// --- ARROW BUTTON ---
// --------------------

.choices::before {
  content: "";
  position: absolute;
  top: 27px;
  right: 35px;
  width: 8px;
  height: 8px;
  background-color: transparent;
  border-top: 1px solid $primary-color;
  border-left: 1px solid $primary-color;
  transform: rotate(45deg);
  cursor: pointer;
  transition: transform 0.3s ease;
}

.choices.is-open[data-type*=select-one]::before {
  transform: rotate(226deg);
}

// -------------------
// --- PLACEHOLDER ---
// -------------------

.choices__list--single {
  width: 100%;
  padding: 19px 19px 18px 19px;
  border: 2px solid $grey-background-color;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  transition: border-color 0.4s ease-in-out,
    background-color 0.4s ease-in-out;

  // --- max-width: 1024px ---
  @include tablet {
    padding: 19px 19px 17px 19px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding: 19px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 19px 19px 14.5px 19px;
  }
}

// --- hide item in placeholder from title ---

.choices__list--dropdown .choices__placeholder {
	display: none;
}

// ---------------------
// --- DROPDOWN LIST ---
// ---------------------

.choices__list {
  columns: 100px 2;
  column-gap: 10px;
  border-radius: 0 10px 10px 0;
  background-color: $white-color;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted {
  color: $primary-light-color;
  background-color: $white-color
}

.choices__list--dropdown {
  position: absolute;
  z-index: -10;
  margin-top: -20px;
  width: 100%;
  padding: 40px 20px 20px 20px;
  border-radius: 10px;
  transition: visibility 0.4s ease-in-out,
    opacity 0.4s ease-in-out;
}

.choices__list.is-active {
  border: 2px solid $grey-background-color;
  border-top: none;
}

// --------------------------
// --- DROPDOWN LIST ITEM ---
// --------------------------

.choices__item {
  color: $primary-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;
  cursor: pointer;
  transition: color 0.4s ease-in-out,
    background-color 0.4s ease-in-out;
}

.choices__item--choice {
  color: $text-color;
}

.choices__item--choice:not(:last-child) {
  margin-bottom: 11px;
}

.choices__item.is-selected {
  display: none;
}

// ------------------------------------
// --- CLOSE AND OPEN DROPDOWN LIST ---
// ------------------------------------

.choices__list--dropdown {
  visibility: hidden;
  opacity: 0;
}

.choices__list--dropdown.is-active {
  visibility: visible;
  opacity: 1;
}

// --- dropdown list transition effects ---

.choices__item:focus-visible {
  color: $primary-dark-color;
  background-color: $primary-extra-light-color;
}

@media(any-hover: hover)  {
  .choices__item:hover {
    color: $primary-light-color;
  }
}

.choices__item:active {
  color: $primary-color;
}

.choices.is-focused .choices__list--single {
  background-color: #f8efff;
  border-color: $primary-color;
}

.choices__list--dropdown.is-open .choices__list--single {
  background-color: #f8efff;
  border-color: red;
}

@media(any-hover: hover) {
  .choices__inner:hover .choices__list--single {
    border-color: $primary-color;
  }
}

.choices__inner:active .choices__list--single {
  border-color: $primary-dark-color;
}

// --- active select block ---

.dropdown-show {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}
