@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin small-desktop {
  @media (max-width: (1230px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin between-tablet {
  @media (max-width: (930px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
