// -------------------------------
// --- HEADER SECTION WRAPPERS ---
// -------------------------------

.header__desktop-wrap {

  // --- max-width: 1024px ---
  @include tablet {
    display: none;
  }
}

.header__tablet-wrap {
  display: none;

  // --- max-width: 1024px ---
  @include tablet {
    display: block;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    display: none;
  }
}

.header__small-tablet-wrap {
  display: none;

  // --- max-width: 768px ---
  @include small-tablet {
    display: block;
  }

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

.header__mobile-wrap {
  display: none;

  // --- max-width: 576px ---
  @include mobile {
    display: block;
  }
}

.header__bg-wrap {
  background-color: $grey-background-color;
}

.header__bg-wrap--mobile-margin {
  margin-bottom: 16px;
}

.header__wrap--demarcated {
  margin-bottom: 5px;
  border-top: 1px solid $grey-background-color;
  border-bottom: 1px solid $grey-background-color;
}

// -------------------------
// --- HEADER CONTAINERS ---
// -------------------------

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__container--level-1 {
  padding-top: 10px;
  padding-bottom: 14px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 12px;
    padding-bottom: 20px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-bottom: 21px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-bottom: 11px;
  }
}

.header__container--level-2 {
  padding-top: 32px;
  padding-bottom: 15px;

  // --- max-width: 1024px ---
  @include tablet {
    flex-wrap: wrap;
    gap: 20px;
    padding-top: 8px;
    padding-bottom: 9px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 15px;
    padding-bottom: 5px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 9px;
    padding-bottom: 7px;
  }
}

.header__container--level-3 {
  padding-top: 13px;
  padding-bottom: 13px;
  gap: 30px;

  // --- max-width: 1024px ---
  @include tablet {
    flex-wrap: wrap;
    padding-top: 31px;
    padding-bottom: 15px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 30px;
    padding-bottom: 16px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 12px 10px 7px 10px;
  }
}

.header__container--level-4 {

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 16px;
    padding-bottom: 14px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding: 14px 59px 11px 66px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 0px var(--container-offset) 13px var(--container-offset);
  }
}

.header__container--level-5 {
  // --- max-width: 768px ---
  @include small-tablet {
    padding: 12px 69px 11px 67px;
  }
}

// ---------------------
// --- PHONE CONTACT ---
// ---------------------

.header__phone-wrap {

  // --- max-width: 576px ---
  @include mobile {
    position: absolute;
    z-index: 15;
    top: 350px;
    left: -100%;
    width: 100%;
    padding-bottom: 0;
    padding-left: 72px;
    visibility: hidden;
    opacity: 0;
    transition: transform 0.7s ease-in-out,
      visibility 0.7s ease-in-out,
      opacity 0.7s ease-in-out;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-left: 15px;
  }
}

.header__phone-wrap.menu--active {
  visibility: visible;
  opacity: 1;
  transform: translateX(100%);
}

// ---------------------------
// --- PAGE TOP-NAVIGATION ---
// ---------------------------

.header__top-nav {

  // --- max-width: 576px ---
  @include mobile {
    position: absolute;
    z-index: 10;
    top: 50px;
    left: -100%;
    width: 100%;
    height: 100vh;
    padding-bottom: 0;
    background-color: $white-color;
    visibility: hidden;
    opacity: 0;
    transition: transform 0.7s ease-in-out,
      visibility 0.7s ease-in-out,
      opacity 0.7s ease-in-out;
  }
}

.header__top-nav.menu--active {
  visibility: visible;
  opacity: 1;
  transform: translateX(100%);
}

.top-nav__list {
  display: flex;
  flex-wrap: wrap;
  gap: 60px;
  padding-top: 1px;

  // --- max-width: 1024px ---
  @include tablet {
    gap: 30px 60px;
    transform: translateX(-2px);
  }

  // --- max-width: 768px ---
  @include small-tablet {
    flex-direction: column;
    gap: 30px;
    width: auto;
    height: 30px;
    overflow-x: auto;
  }

  // --- max-width: 576px ---
  @include mobile {
    flex-direction: column;
    gap: 41px;
    height: auto;
    padding: 70px 15px;
  }
}

.top-nav__link {
  @include link-text;
  padding: 0 2px;
  color: $grey-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;
}

// -----------------
// --- MAIN LOGO ---
// -----------------

.header__logo-wrap {

  // --- max-width: 768px ---
  @include small-tablet {
    align-self: flex-end;
    transform: translateX(-4px);
  }

  // --- max-width: 576px ---
  @include mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    transform: translateX(0px);
  }
}

// ---------------------------
// --- PAGE SUB-NAVIGATION ---
// ---------------------------

.header__sub-nav {

  // --- max-width: 768px ---
  @include small-tablet {
    width: 100%;
  }
}

.sub-nav__list {
  display: flex;
  flex-wrap: wrap;
  gap: 88px;
  transform: translateX(-2px);

  // --- max-width: 1440px ---
  @include big-desktop {
    gap: 28px 38px;
  }

  // --- max-width: 1024px ---
  @include tablet {
    transform: translate(0px, -1px);
  }

  // --- max-width: 768px ---
  @include small-tablet {
    flex-direction: column;
    gap: 30px;
    width: auto;
    height: 30px;
    overflow-x: auto;
  }

  // --- max-width: 576px ---
  @include mobile {
    gap: 27px;
  }
}

.sub-nav__link {
  @include link-text;
  padding: 0 2px;
  color: $text-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 100%;

  // --- max-width: 768px ---
  @include small-tablet {
    font-size: 14px;
  }
}

.sub-nav__link:focus-visible {
  color: $text-color;
}

