.catalog {
  padding-top: 14px;
  padding-bottom: 69px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-bottom: 70px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 16px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 12px;
  }
}

.catalog__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, auto);
  column-gap: 30px;
  grid-template-areas:
  "filter-title title title title"
  "filter list list list"
  ". list list list"
  "banner list list list"
  ". . control .";

  // --- max-width: 1024px ---
  @include tablet {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(6, auto);
    grid-template-areas:
    "title title title"
    "filter filter filter"
    "list list list"
    "list list list"
    "list list list"
    ". control .";
  }
}

// --------------------
// --- FILTER TITLE ---
// --------------------

.catalog__filter-title {
  grid-area: filter-title;
}

// -----------------------
// --- LIST OF FILTERS ---
// -----------------------

.catalog__filter-wrap {
  grid-area: filter;
}

// --- the rest styles for filter are in filter.scss file ---

// -------------------------------------
// --- SECTION TITLE AND FILTER TAGS ---
// -------------------------------------

.catalog__title-wrap {
  grid-area: title;
  display: flex;
  gap: 19px;
  margin-bottom: 13px;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 24px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 25px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 24px;
  }
}

.catalog__title {
  color: $text-color;
  font-size: 24px;
  font-weight: 400;
  line-height: 130%;
}

// --- all styles for filter tags are in tag.scss file ---

// --------------------
// --- CATALOG LIST ---
// --------------------

.catalog__list {
  @include product-list(3, 32px);
  display: none;
  grid-area: list;
  padding-top: 12px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 21px;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    @include product-list(2, 32px);
    display: none;
  }

  // --- max-width: 576px ---
  @include mobile {
    @include product-list(2, 16px);
    display: none;
    padding-top: 27px;
  }
}

.catalog__item:nth-last-child(-n+3) {

  // --- max-width: 930px ---
  @include between-tablet {
    display: none;
  }
}

.list-active {
  display: grid;
}

// --- the other catalog cards styles are in elements.scss file ---

// -----------------
// --- AD BANNER ---
// -----------------

.catalog__banner {
  grid-area: banner;

  // --- max-width: 1024px ---
  @include tablet {
    display: none;
  }
}

.banner-ctlg {
  max-width: 296px;
  background-color: $text-color;
  border-radius: 10px;
}

.banner-ctlg__text-wrap {
  padding: 34px 30px 29px 30px;
}

.banner-ctlg__title {
  margin-bottom: 2px;
  color: $banner-title-color;
  font-family: $second-font-family;
  font-size: 24px;
  font-weight: 900;
}

.banner-ctlg__descr {
  display: inline-block;
  max-width: 200px;
  margin-bottom: 15px;
  color: $grey-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 126%;
}

// -----------------------
// --- CONTROL BUTTONS ---
// -----------------------

.catalog__control {
  grid-area: control;
  justify-self: center;
  padding-top: 32px;

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 33px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 34px;
  }
}

.slider-ctrl {
  display: flex;
  gap: 16px;
}

.slider-ctrl__btn {
  padding: 7px 10px;
  color: $primary-color;
  font-size: 16px;
  font-weight: 600;
  line-height: 100%;
  text-align: center;
  border: 2px solid $primary-color;
  border-radius: 10px;
  transition: background-color 0.4s linear,
    color 0.4s linear,
    border-color 0.4s linear;

  // --- max-width: 576px ---
  @include mobile {
    padding: 6px 9px;
  }
}

.slider-ctrl__btn:last-child {
  display: none;

  // --- max-width: 930px ---
  @include between-tablet {
    display: block;
  }
}

// --- transitions of control buttons ---

.slider-ctrl__btn:focus-visible {
  color: $white-color;
  background-color: $primary-light-color;
}

@media (any-hover: hover) {
  .slider-ctrl__btn:hover {
    color: $white-color;
    background-color: $primary-color;
  }

  .slider-ctrl__btn:hover.slider-ctrl__btn--active {
    color: $white-color;
    background-color: $primary-dark-color;
    border-color: $primary-dark-color;
  }
}

.slider-ctrl__btn:active,
.slider-ctrl__btn--active {
  color: $white-color;
  background-color: $primary-dark-color;
  border-color: $primary-dark-color;
}
