.footer {
  padding-top: 24px;
  background-color: $disabled-color;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 24px;
  }
}

// -----------------------------------
// --- MAIN CONTENT FOOTER SECTION ---
// -----------------------------------

.footer__content {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 15px;
  padding-bottom: 17px;

  // --- max-width: 1024px ---
  @include tablet {
    grid-template-columns: 2fr 8fr;
    gap: 32px;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, auto);
    gap: 13px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-bottom: 19px;
  }
}

// ------------------------------------
// --- FOOTER LOGO AND SOCIAL ICONS ---
// ------------------------------------

.footer__graph-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 1 / span 2;
    flex-direction: row;
    max-width: 80vw;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    max-width: 578px;
  }

  // --- max-width: 576px ---
  @include mobile {
    grid-column: 1 / span 2;
    flex-direction: column;
    align-items: center;
    gap: 21px;
  }
}

// -------------------
// --- FOOTER LOGO ---
// -------------------

.footer__logo-wrap {

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 2px;
  }
}

// --------------------
// --- SOCIAL ICONS ---
// --------------------

.socials {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  transform: translateX(-3px);

  // --- max-width: 930px ---
  @include between-tablet {
    transform: translateX(0);
    transform: translateY(-4px);
  }
}

.socials__link {
  font-size: 0;
}

.socials__icon {
  width: 36px;
  height: 36px;
  padding: 3px;
  fill: $grey-color;
  background-color: transparent;
  transition: fill 0.4s ease-in-out,
    background-color 0.4s ease-in-out;
}

.socials__link:focus-visible .socials__icon {
  background-color: $primary-light-color;
  fill: $white-color;
}

@media (any-hover: hover) {
  .socials__link:hover .socials__icon {
    fill: $primary-color;
  }
}

.socials__link:active .socials__icon {
  fill: $primary-dark-color;
}

// -------------------
// --- FOOTER MENU ---
// -------------------

.footer__text-wrap {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  padding: 4px 0;

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 1 / span 2;
    grid-row: 2 / 3;
  }

  // --- max-width: 576px ---
  @include mobile {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 26px 15px;
  }
}

// --------------------------------
// --- ONE COLUMN OF LINKS LIST ---
// --------------------------------

.footer__menu-title {
  margin-bottom: 3px;
  color: $text-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 100%;
}

.ftr-menu__item:not(:last-child) {
  margin-bottom: 6px;
}

.ftr-menu__link {
  @include link-text;
  color: $grey-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;
}

// --------------------------------
// --- COPYRIGHT FOOTER SECTION ---
// --------------------------------

.footer__copy {
  position: relative;
  left: 50%;
  right: -37%;
  width: 100vw;
  margin-left: -50vw;
  margin-right: -50vw;
  background-color: $grey-background-color;
}

.footer__copy-text {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 7px;
  padding-bottom: 6px;
  color: $grey-color;
  font-size: 14px;
  font-weight: 400;
  word-spacing: 7px;
}
