.feedback__form {
  max-width: 624px;
}

// ---------------------
// --- FEEDBACK FORM ---
// ---------------------

.form {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 30px;
}

.form__label {
  position: relative;
  width: 100%;
}

.form__input {
  width: 100%;
  padding: 17px 18px;
  border: 1px solid $grey-background-color;
  border-radius: 10px;
  background-color: $grey-background-color;
  transition: background-color 0.4s ease-in-out,
    border-color 0.4s ease-in-out,
    color 0.4s ease-in-out;
}

.form__input::placeholder {
  color: $grey-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 250%;
}

.form__btn {
  margin-bottom: 11px;
  padding: 18px 32px;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 13px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 13px;
  }
}

// --- form fields transitions ---

.form__input:focus-visible {
  border-color: $primary-color;
  background-color: #f8efff;
}

@media (any-hover: hover) {
  .form__input:hover {
    border-color: $disabled-color;
    background-color: $disabled-color;
  }
}

.form__input:active {
  color: $text-color;
  border-color: #b8ec64;
  background-color: $grey-background-color;
}

.form__input:not(:placeholder-shown) {
  border-color: #b8ec64;
  background-color: $grey-background-color;
}

// --- input error ---

.error-color {
  color: $error-color;
}

.form__input.just-validate-error-field {
  border-color: $error-color !important;
}

.just-validate-error-label {
  position: absolute;
  top: -17px;
  left: 18px;
  color: $error-color;
  font-size: 12px;
  font-weight: 400;
}

.custom-checkbox + .just-validate-error-label {
  position: relative;
  top: -50px;
  left: 22px;
}

// ------------------------
// --- CHECKBOX SECTION ---
// ------------------------

.custom-checkbox {
  font-size: 0;
  cursor: pointer;
  transform: translateY(-29px);
}

.custom-checkbox__field {
  width: 0;
  height: 0;
  opacity: 0;
}

.custom-checkbox__content {
  position: relative;
  top: 33px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 7px;
  color: $text-color;
  font-size: 14px;
  font-weight: 400;
  transform: translateY(-34px);

  // --- max-width: 576px ---
  @include mobile {
    gap: 5px;
    font-size: 12px;
  }
}

// -----------------------
// --- CUSTOM CHECKBOX ---
// -----------------------

.custom-checkbox__content::before {
  @include checkbox-square;
}

.custom-checkbox__content::after {
  @include checkbox-checkmark;
}

.custom-checkbox__field:focus-visible + .custom-checkbox__content::before {
  outline: 1px solid $primary-color;
  outline-offset: 3px;
}

.custom-checkbox__field:checked + .custom-checkbox__content::before {
  background-color: $primary-color;
  border-color: $primary-color;
  outline: none;
}

.custom-checkbox__field:checked + .custom-checkbox__content::after {
  transform: scale(1);
}

// ---------------------------
// --- USER AGREEMENT LINK ---
// ---------------------------

.custom-checkbox__agreement {
  color: $primary-color;
  text-decoration: underline;
  transition: color 0.4s ease-in-out;
}

.custom-checkbox__agreement:focus-visible {
  color: $primary-light-color;
}

@media (any-hover: hover) {
  .custom-checkbox__agreement:hover {
    color: $primary-light-color;
  }
}

.custom-checkbox__agreement:active {
  color: $primary-dark-color;
}
