.header__buttons {

  // --- max-width: 768px ---
  @include small-tablet {
    align-self: flex-end;
  }
}

.user-panel {
  display: flex;
  gap: 24px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 1px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 0;
  }
}

.user-panel__btn {
  padding: 2px 4px;
  font-size: 0;
  transition: background-color 0.5s ease-in-out;
}

.user-panel__icon {
  fill: $primary-color;
  transition: fill 0.5s ease-in-out;
}

// --- buttons transition effects ---

.user-panel__btn:focus-visible {
  background-color: $primary-light-color;
}

.user-panel__btn:focus-visible .user-panel__icon {
  fill: $white-color;
}

.user-panel__btn:hover .user-panel__icon {
  @media (any-hover: hover) {
    &:hover {
      fill: $primary-light-color;
    }
  }
}

.user-panel__btn:active .user-panel__icon {
  fill: $primary-dark-color;
}

// --------------------
// --- LOGIN BUTTON ---
// --------------------

.user-panel__icon--login {
  width: 26px;
  height: 28px;
}

// ----------------------------
// --- SHOPPING CART BUTTON ---
// ----------------------------

.user-panel__btn--cart {
  position: relative;
  padding: 1px 0 2px 15px;
}

.user-panel__icon--cart {
  width: 30px;
  height: 28px;
}

.user-panel__counter {
  position: absolute;
  top: 7px;
  left: 4px;
  display: inline-block;
  padding: 3px 4px;
  text-align: center;
  border-radius: 100%;
  color: $white-color;
  font-size: 10px;
  font-weight: 700;
  line-height: 96%;
  background-color: $gold-color;
}
