.banner {
  min-height: 600px;
  background-image: url("../img/1920-main-banner.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  // --- max-width: 1024px ---
  @include tablet {
    background-image: url("../img/1024-main-banner.png");
  }

  // --- max-width: 768px ---
  @include small-tablet {
    background-position: left;
  }

  // --- max-width: 576px ---
  @include mobile {
    min-height: 0;
    background-image: unset;
  }
}

.banner__container {
  display: flex;
  align-items: center;
  min-height: 600px;
  padding-top: 15px;
  padding-bottom: 15px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 18px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 22px;
  }

  // --- max-width: 576px ---
  @include mobile {
    display: block;
    min-height: 300px;
    padding-top: 19px;
  }
}

.banner__wrap {
  max-width: 508px;

  // --- max-width: 768px ---
  @include small-tablet {
    transform: translateX(-36px);
  }

  // --- max-width: 576px ---
  @include mobile {
    max-width: 100%;
    transform: translateX(0px);
  }
}

.banner__title {
  margin-bottom: 4px;
  color: $banner-title-color;
  font-family: $second-font-family;
  font-size: 60px;
  font-weight: 900;
  line-height: 120%;

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

.banner__descr {
  margin-bottom: 50px;
  color: $white-color;
  font-size: 48px;
  font-weight: 400;

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 52px;
  }

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

.banner__btn {
  padding: 21px 30px;

  // --- max-width: 576px ---
  @include mobile {
    width: 100%;
    text-align: center;
  }
}

// ---------------------------
// --- MOBILE SECTION ONLY ---
// ---------------------------

.banner__mobile-bg {
  display: none;

  // --- max-width: 576px ---
  @include mobile {
    position: relative;
    display: block;
    width: 102vw;
    top: -18px;
    left: 0%;
    min-height: 66vw;
    margin-right: -64vw;
    margin-bottom: 13px;
    margin-left: -4vw;
    padding: 9vw 4vw;
    background-image: url("../img/320-main-banner.png");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
  }
}

.banner__title--mobile {

  // --- max-width: 576px ---
  @include mobile {
    display: block;
    margin-bottom: 0;
    font-size: 7vw;
    line-height: 145%;
  }
}

.banner__descr--mobile {

  // --- max-width: 576px ---
  @include mobile {
    display: block;
    max-width: 60vw;
    margin-bottom: 0;
    font-size: 5vw;
  }
}
