.tag {
  display: flex;
  flex-wrap: wrap;
  gap: 21px;

  // --- max-width: 768px ---
  @include small-tablet {
    display: none;
  }
}

.tag__item {
  transition: all 0.3s ease;
}

.tag__item:focus-within {
  outline: 1px solid $primary-color;
  outline-offset: 3px;
  border-radius: 3px;
}

.tag__wrap {
  display: flex;
  align-items: center;
  gap: 7px;
  padding: 6px 9px;
  border-radius: 3px;
}

.tag__wrap--bg-color-1 {
  background-color: #daffd1;
}

.tag__wrap--bg-color-2 {
  background-color: #fff5d1;
}

.tag__wrap--bg-color-3 {
  background-color: #ebd1ff;
}

.tag__wrap--bg-color-4 {
  background-color: #eaeaea;
}

.tag__text {
  color: $grey-dark-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 130%;
}

// ------------------
// --- TAG BUTTON ---
// ------------------

.tag__btn {
  position: relative;
  display: block;
  width: 10px;
  height: 10px;
}

.tag__btn-line {
  position: absolute;
  right: 0;
  display: inline-block;
  width: 100%;
  height: 1px;
  background-color: $grey-dark-color;
  transition: background-color 0.4s ease-in-out;
}

.tag__btn-line:first-child {
  top: 50%;
  transform: rotate(45deg);
}

.tag__btn-line:last-child {
  top: 50%;
  transform: rotate(-45deg);
}
