@mixin checkbox-square {
  content: "";
  position: relative;
  display: block;
  top: 0;
  left: 0;
  width: 14px;
  height: 14px;
  border: 1px solid $grey-color;
  border-radius: 2px;
  background-color: transparent;
  transition: background-color 0.4s ease-in-out,
    border-color 0.4s ease-in-out;
}
