// ----------------------
// --- TOOLTIP BUTTON ---
// ----------------------

.feedback__tooltip-btn {
  position: absolute;
  bottom: 2px;
  width: 15px;
  height: 14px;
  margin-left: 4px;
  color: $gold-color;
  font-size: 10px;
  font-weight: 700;
  background-color: transparent;
  border: 2px solid $gold-color;
  border-radius: 50%;
  transition: color 0.4s ease-in-out,
    background-color 0.4s ease-in-out;

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

// --- tooltip button transitions ---

.feedback__tooltip-btn:focus-visible {
  color: $white-color;
  background-color: $gold-color;
}

@media (any-hover: hover) {
  .feedback__tooltip-btn:hover {
    color: $white-color;
    background-color: $gold-color;
  }
}

.feedback__tooltip-btn:active {
  color: $white-color;
  background-color: $gold-color;
}

// -------------------
// --- POP-UP TEXT ---
// -------------------

.feedback__tooltip-txt {
  z-index: 10;
  max-width: 160px;
  padding: 6px;
  color: $white-color;
  font-size: 9px;
  font-weight: 400;
  line-height: 156%;
  text-align: center;
  background-color: $text-color;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease 0.4s,
    opacity 0.3s ease 0.4s;

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

// ---------------------
// --- TOOLTIP ARROW ---
// ---------------------

.feedback__tooltip-arrow,
.feedback__tooltip-arrow::before {
  position: absolute;
  width: 11px;
  height: 11px;
  background-color: inherit;
}

.feedback__tooltip-arrow {
  left: -5px !important;
  visibility: hidden;
}

.feedback__tooltip-arrow::before {
  content: "";
  visibility: visible;
  transform: rotate(45deg);
}

.feedback__tooltip-txt[data-popper-placement^='top'] > .feedback__tooltip-arrow {
  bottom: -5px;
}

.feedback__tooltip-txt[data-popper-placement^='bottom'] > .feedback__tooltip-arrow {
  top: -5px;
}

.feedback__tooltip-txt[data-popper-placement^='left'] > .feedback__tooltip-arrow {
  right: -5px;
}

.feedback__tooltip-txt[data-popper-placement^='right'] > .feedback__tooltip-arrow {
  left: -5px;
}

// ----------------------
// --- ACTIVE TOOLTIP ---
// ----------------------

.feedback__tooltip-txt[data-show] {
  visibility: visible;
  opacity: 1;
}
