// --------------------
// --- PRODUCT CARD ---
// --------------------

.card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 15px;
  height: 100%;
  padding: 30px 16px;
  border-radius: 10px;
  background-color: $grey-background-color;

  // --- max-width: 576px ---
  @include mobile {
    padding: 16px;
  }
}

// --- card image ---

.card__img-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 164px;

  // --- max-width: 576px ---
  @include mobile {
    min-height: 24vw;
  }
}

// --- card rating ---

.card__rating {
  align-self: flex-start;
  display: flex;
  align-items: center;
  padding: 0 14px 11px 14px;
  gap: 6px;
  color: $gold-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;

  // --- max-width: 576px ---
  @include mobile {
    padding: 0 0 0 0;
    font-size: 12px;
  }
}

.card__rating-icon {
  width: 16px;
  height: 15px;
  fill: $gold-color;

  // --- max-width: 576px ---
  @include mobile {
    width: 13px;
    height: 12px;
  }
}

.card__img--transform {
  width: 100%;
}

// --- card text content ---

.card__info-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  flex-basis: 100%;
  height: 100%;
  padding: 0 13px;

  // --- max-width: 576px ---
  @include mobile {
    padding: 0;
  }
}

.card__txt-wrap {
  margin-bottom: 22px;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 17px;
  }
}

.card__title {
  margin-bottom: 22px;
  color: $text-color;
  font-size: 24px;
  font-weight: 400;
  line-height: 130%;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 16px;
    font-size: 16px;
  }
}

.card__price {
  color: $grey-dark-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;
}

.btn-primary.card__btn {

  // --- max-width: 576px ---
  @include mobile {
    padding: 9px 23px;
  }
}

// -------------------
// --- CARD BUTTON ---
// -------------------

.btn-primary {
  display: inline-block;
  padding: 17px 33px;
  color: $primary-color;
  font-size: 16px;
  font-weight: 600;
  line-height: 100%;
  border: 2px solid $primary-color;
  border-radius: 10px;
  background-color: transparent;
  transition: background-color 0.4s ease-in-out,
    border-color 0.4s ease-in-out,
    color 0.4s ease-in-out;
}

// --- button transitions effects ---

.btn-primary:focus-visible {
  color: $white-color;
  border-color: $primary-color;
  background-color: $primary-light-color;
}

@media (any-hover: hover) {
  .btn-primary:hover {
    color: $white-color;
    border-color: $primary-color;
    background-color: $primary-color;
  }
}

.btn-primary:active {
  color: $white-color;
  border-color: $primary-dark-color;
  background-color: $primary-dark-color;
}

// ------------------------------
// --- BUTTON WITH BACKGROUND ---
// ------------------------------

.btn-filled {
  display: inline-block;
  padding: 21px 49px;
  color: $white-color;
  font-size: 16px;
  font-weight: 700;
  line-height: 100%;
  background-color: $primary-color;
  border: 2px solid $primary-color;
  border-radius: 10px;
  transition: background-color 0.4s ease-in-out,
    border-color 0.4s ease-in-out;
}

.btn-filled:disabled {
  color: $grey-color;
  background-color: $disabled-color;
  border-color: $disabled-color;
}

// --- button transitions effects ---

.btn-filled:focus-visible {
  background-color: $primary-light-color;
  border-color: $primary-dark-color;
}

@media (any-hover: hover) {
  .btn-filled:hover {
    background-color: $primary-light-color;
    border-color: $primary-light-color;
  }
}

.btn-filled:active {
  background-color: $primary-dark-color;
  border-color: $primary-dark-color;
}

// -----------------------
// --- CONTROL BUTTONS ---
// -----------------------

.control {
  display: flex;
  align-items: flex-start;
  gap: 13px;
}

.control__btn {
  font-size: 0;
  cursor: pointer;
}

.control__icon {
  width: 32px;
  height: 32px;
  fill: transparent;
  transition: fill 0.4s ease-in-out;
}

.control__icon circle,
.control__icon path {
  stroke: $primary-color;
  transition: stroke 0.4s ease-in-out;
}

.control__btn:first-child {
  transform: rotateY(180deg);
}

.control__btn:disabled .control__icon circle,
.control__btn:disabled .control__icon path {
  stroke: $grey-color;
  transition: stroke 0.4s ease-in-out;
}

// --- button transitions effects ---

.control__btn:focus-visible .control__icon {
  fill: $primary-light-color;
}

.control__btn:focus-visible .control__icon path {
  stroke: $white-color;
}

@media (any-hover: hover) {
  .control__btn:hover .control__icon {
    fill: $primary-color;
  }

  .control__btn:hover .control__icon path {
    stroke: $white-color;
  }

  .control__btn:disabled:hover .control__icon {
    fill: transparent;
  }

  .control__btn:disabled:hover .control__icon circle,
  .control__btn:disabled:hover .control__icon path {
    stroke: $grey-color;
  }
}

.control__btn:active .control__icon {
  fill: $primary-dark-color;
}

.control__btn:active .control__icon circle {
  stroke: $primary-dark-color;
}

.control__btn:active .control__icon path {
  stroke: $white-color;
}

// -------------------------
// --- MAIN LOGO OUTLINE ---
// -------------------------

.logo {
  display: inline-block;
  outline: 2px solid transparent;
  outline-offset: 3px;
  border-radius: 2px;
}

.logo:focus-visible {
  outline-color: $grey-color;
}

// ------------------
// --- PHONE LINK ---
// ------------------

.phone-link {
  display: inline-block;
  padding: 3px;
  color: $gold-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;
  background-color: transparent;
  transition: color 0.4s ease-in-out,
    background-color 0.4s ease-in-out;
}

.phone-link__icon {
  width: 18px;
  height: 18px;
  fill: $gold-color;
  transform: translateY(4px);
  transition: fill 0.4s ease-in-out;
}

.phone-link :focus-visible {
  background-color: #fff5d1;
}

@media (any-hover: hover) {
  .phone-link :hover .phone-link__icon,
  .phone-link :hover {
    color: #ffbd59;
    fill: #ffbd59;
  }
}

.phone-link :active .phone-link__icon,
.phone-link :active {
  color: #bb370e;
  fill: #bb370e;
}

// -----------------------
// --- ARROW ICON LINK ---
// -----------------------

.arrow-link {
  position: relative;
  display: flex;
  align-items: center;
  gap: 4px;
  max-width: 117px;
  color: $primary-color;
  font-size: 16px;
  font-weight: 700;
  line-height: 120%;
  transition: color 0.4s ease-in;
}

.arrow-link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 81px;
  height: 2px;
  background-color: transparent;
  transition: background-color 0.6s ease-in-out;
}

.arrow-link__icon {
  width: 23px;
  height: 23px;
  fill: transparent;
  transition: stroke 0.4s ease-in,
    fill 0.4s ease-in;
}

.arrow-link__icon circle,
.arrow-link__icon path {
  transition: stroke 0.4s ease-in;
}

// --- transitions effects ---

.arrow-link:focus-visible::after {
  background-color: $primary-color;
}

.arrow-link:focus-visible .arrow-link__icon {
  fill: $primary-light-color;
}

.arrow-link:focus-visible .arrow-link__icon path {
  stroke: $white-color;
}

@media (any-hover: hover) {
  .arrow-link:hover::after {
    background-color: $primary-color;
  }

  .arrow-link:hover .arrow-link__icon {
    fill: $primary-color;
  }

  .arrow-link:hover .arrow-link__icon path {
    stroke: $white-color;
  }
}

.arrow-link:active {
  color: $primary-dark-color;
}

.arrow-link:active::after {
  background-color: $primary-dark-color;
}

.arrow-link:active .arrow-link__icon {
  fill: $primary-dark-color;
}

.arrow-link:active .arrow-link__icon circle {
  stroke: $primary-dark-color;
}

.arrow-link__link:active .arrow-link__icon path {
  stroke: $white-color;
}
