.same {
  padding-top: 33px;
  padding-bottom: 71px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 21px;
    padding-bottom: 70px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-bottom: 71px;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    padding-top: 10px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 30px;
    padding-bottom: 69px;
  }
}

.same__top-wrap {
  display: flex;
  justify-content: space-between;
  margin-bottom: 22px;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 24px;
  }
}

// ---------------------
// --- SECTION TITLE ---
// ---------------------

.same__title {
  color: $text-color;
  font-size: 24px;
  font-weight: 400;
  line-height: 130%;
}

// -----------------------
// --- CONTROL BUTTONS ---
// -----------------------

.same__control {

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

// --------------
// --- SWIPER ---
// --------------

.swiper-same__item {
  height: auto;
}
