// --------------------------------------
// --- EMPTY ELEMENT ON THE MAIN PAGE ---
// --------------------------------------

.breadcrumb-empty {
  padding-bottom: 18px;

  // --- max-width: 576px ---
  @include mobile {
    padding-bottom: 21px;
  }
}

// -----------------------------
// --- BREADCRUMB NAVIGATION ---
// -----------------------------

.breadcrumb {
  padding-top: 13px;
  padding-bottom: 10px;

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

.breadcrumb__nav {
  transform: translateY(-7px);

  // --- max-width: 768px ---
  @include small-tablet {
    transform: translateY(-5px);
  }
}

.breadcrumb__list {
  display: flex;
  flex-wrap: wrap;
  gap: 1px;
}

.breadcrumb__link {
  @include link-text;
  padding: 0 2px;
  color: $grey-color;
  font-size: 14px;
  line-height: 100%;
  font-weight: 400;
}

.breadcrumb__link--active {
  color: $primary-dark-color;
}

// ---------------------
// --- DIVIDING LINE ---
// ---------------------

.breadcrumb__item:not(:last-child)::after {
  content: "/";
  display: inline-block;
  color: $grey-color;
  font-size: 14px;
  font-weight: 400;
}
