.category {
  padding-top: 69px;
  padding-bottom: 50px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 70px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 71px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 53px;
    padding-bottom: 48px
  }
}

.category__title {
  @include title-section;
  margin-bottom: 27px;
}

// --------------------
// --- PRODUCT LIST ---
// --------------------

.category-list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 32px;

  // --- max-width: 1024px ---
  @include tablet {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }

  // --- max-width: 576px ---
  @include mobile {
    grid-template-rows: repeat(3, auto);
    gap: 32px 16px;
  }
}

// --------------------
// --- PRODUCT CARD ---
// --------------------

// --- sofa ---
.category-list__item:nth-child(1) {
  grid-column: 1 / span 3;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 1 / span 2;
  }
}

// --- armchair ---
.category-list__item:nth-child(2) {
  grid-column: 4 / span 3;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
  }
}

// --- bed ---
.category-list__item:nth-child(3) {
  grid-column: 1 / span 2;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
  }
}

// --- dresser ---
.category-list__item:nth-child(4) {
  grid-column: 3 / span 2;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 1 / 2;
    grid-row: 3 / 4;
  }
}

// --- chair ---
.category-list__item:nth-child(5) {
  grid-column: 5 / span 2;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 3 / 4;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
  }
}

.category-list__wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 27px 31px 29px 31px;
  background-color: $grey-background-color;
  border-radius: 10px;

  // --- max-width: 1024px ---
  @include tablet {
    padding: 26px 31px 29px 31px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-bottom: 17px;
  }
}

.category-list__item:not(:first-child) .category-list__wrap {

  // --- max-width: 768px ---
  @include small-tablet {
    padding: 27px 31px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 18px 17px 15px 17px; // ???
  }
}

// ----------------------------
// --- PRODUCT CARD CONTENT ---
// ----------------------------

.category-list__top-wrap {
  margin-bottom: 36px;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 37px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 35px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 18px;
  }
}

.category-list__item:not(:first-child) .category-list__top-wrap {

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 34px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 18px;
  }
}

// -------------------------
// --- SUB-CATEGORY LIST ---
// -------------------------

.category-list__sub-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 30px;
  margin-bottom: 13px;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 12px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 13px;
  }

  // --- max-width: 576px ---
  @include mobile {
    gap: 10px 20px;
    margin-bottom: 12px;
  }
}

.category-list__item:not(:first-child) .category-list__sub-list {

  // --- max-width: 576px ---
  @include mobile {
    flex-direction: column;
    gap: 4px;
    margin-bottom: 5px;
  }
}

.category-list__item:not(:first-child) .category-list__sub-item {

  // --- max-width: 576px ---
  @include mobile {
    font-size: 0;
  }
}

.category-list__sub-link {
  @include link-text;
  color: $grey-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;
}

.category-list__item:not(:first-child) .category-list__sub-link {

  // --- max-width: 576px ---
  @include mobile {
    font-size: 10px;
  }
}

// -----------------------------------
// --- CARD TITLE / LINK IN MOBILE ---
// -----------------------------------

.category-list__title {

  // --- max-width: 576px ---
  @include mobile {
    max-width: 125px;
  }
}

.category-list__title-link {
  display: inline-block;
  color: $text-color;
  font-size: 24px;
  font-weight: 400;
  transform: translate(-2px, 4px);
  transition: color 0.4s ease-in-out;
  pointer-events: none;

  // --- max-width: 576px ---
  @include mobile  {
    pointer-events: auto;
  }
}

.category-list__item:not(:first-child) .category-list__title-link {

  // --- max-width: 576px ---
  @include mobile {
    position: relative;
    display: inline;
    font-size: 16px;
    line-height: 104%;
  }
}

.category-list__title-icon {
  display: none;
  width: 22px;
  height: 22px;
  fill: transparent;
  stroke: $primary-color;
  cursor: pointer;
  transform: translateY(4px);
  transition: transform 0.4s ease-in-out 0.2s,
    stroke 0.4s ease-in-out;

  // --- max-width: 576px ---
  @include mobile {
    display: inline-block;
  }
}

.category-list__item:not(:first-child) .category-list__title-icon {

  // --- max-width: 576px ---
  @include mobile {
    position: absolute;
    right: -28px;
    bottom: 3px;
  }
}

.category-list__title-link:focus-visible {
  color: $primary-light-color;
}

.category-list__title-link:focus-visible .category-list__title-icon {
  stroke: $primary-light-color;
  transform: translate(8px, 4px);
}

// ------------------
// --- CARD IMAGE ---
// ------------------

.category-list__img {
  width: calc(100% + 62px);
  max-width: initial;
  margin: 0 -31px 29px -31px;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 30px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 0;
  }
}

.category-list__item:not(:first-child) .category-list__img {

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 0px;
    width: calc(100% + 32px);
    max-width: initial;
    margin-left: -16px;
    margin-right: -16px;
    margin-bottom: 0px;
  }
}

// -------------------
// --- CARD BUTTON ---
// -------------------

.category-list__link {

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}
