.offer {
  padding-top: 70px;
  padding-bottom: 35px;

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 53px;
    padding-bottom: 37px;
  }
}

.offer__top-wrap {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  margin-bottom: 26px;

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 28px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 27px;
  }
}

.offer__control {

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

// ---------------------------
// --- OFFER SECTION TITLE ---
// ---------------------------

.offer__title {
  @include title-section;
}

// --------------------------
// --- OFFER ITEMS SWIPER ---
// --------------------------

.offer-list {

  // --- max-width: 576px ---
  @include mobile {
    flex-direction: column;
    gap: 23px;
  }
}

// --------------------
// --- PRODUCT CARD ---
// --------------------

.offer-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  width: 296px;
  height: auto;
  padding: 37px 30px 30px 30px;
  border-radius: 10px;
  background-color: $grey-background-color;

  // --- max-width: 1024px ---
  @include tablet {
    padding: 63px 30px 30px 30px;
  }

  // --- max-width: 576px ---
  @include mobile {
    min-height: 511px;
    padding: 72px 29px 29px 29px;
  }
}

.offer-card--big {
  width: 624px;
}

// ------------------------
// --- SELL PRICE LABEL ---
// ------------------------

.offer-card__label {
  position: absolute;
  top: 31px;
  left: 0;
  display: inline-block;
  width: 83px;
  height: 34px;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 32px;
  text-align: center;
  background-color: #ff862f;

  // --- max-width: 768px ---
  @include small-tablet {
    top: 29px;
  }

  // --- max-width: 576px ---
  @include mobile {
    top: 31px;
  }
}

.offer-card__label::after {
  content: "";
  position: absolute;
  top: 0%;
  left: 100%;
  width: 0;
  height: 0;
  border: 17px solid transparent;
  border-left: 10px solid #ff862f;
}

// ------------------
// --- CARD IMAGE ---
// ------------------

.offer-card__img-wrap {
  align-self: center;
  margin-bottom: 16px;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 12px;
  }
}

.offer-card--big .offer-card__img-wrap {

  // --- max-width: 1440px ---
  @include big-desktop {
    margin-bottom: 33px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 51px;
    padding-top: 47px;
  }
}

// -----------------------------------
// --- BLOCK WITH TITLE AND BUTTON ---
// -----------------------------------

.offer-card__text-wrap {
  padding: 0;
}

// ------------------
// --- CARD TITLE ---
// ------------------

.offer-card__title {
  margin-bottom: 22px;
  color: $text-color;
  font-size: 24px;
  font-weight: 400;
  line-height: 130%;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 21px;
    font-size: 24px;
  }
}

// --------------------------------
// --- BLOCK WITH OLD/NEW PRICE ---
// --------------------------------

.offer-card__price-wrap {
  display: flex;
  align-items: center;
  gap: 30px;
  margin-bottom: 22px;
}

.offer-card__price {
  color: $grey-dark-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;
}

.offer-card__price--new {
  color: $gold-color;
  font-size: 18px;
}

.offer-card__price--ex {
  color: $grey-color;
  text-decoration: line-through;
}

// ---------------------
// --- PATTERN IMAGE ---
// ---------------------

.offer-card__pattern-img {
  position: absolute;
  right: 30px;
  bottom: 31px;

  // --- max-width: 1440px ---
  @include big-desktop {
    display: none;
  }
}
