.popular {
  padding-top: 35px;
  padding-bottom: 73px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-bottom: 71px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-bottom: 72px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 33px;
    padding-bottom: 70px;
  }
}

.popular__title {
  @include title-section;
  margin-bottom: 25px;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 27px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 26px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 26px;
  }
}

// --------------------
// --- PRODUCT LIST ---
// --------------------

.popular__list {
  @include product-list(4, 32px);

  // --- max-width: 1440px ---
  @include big-desktop {
    @include product-list(3, 32px);
  }

  // --- max-width: 930px ---
  @include between-tablet {
    @include product-list(2, 32px);
  }

  // --- max-width: 576px ---
  @include mobile {
    gap: 17px;
  }
}

.popular__item:nth-last-child(-n+2) {

  // --- max-width: 1440px ---
  @include big-desktop {
    display: none;
  }
}

