*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --content-width: 1280px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
}

.page {
  min-width: 320px;
  min-height: 100%;
  height: 100%;
  font-family: $font-family;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  font-size: 16px;
  background-color: $white-color;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  // --- max-width: 1440px ---
  @include big-desktop {
    --container-offset: 36px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    --container-offset: 72px;
  }

  // --- max-width: 576px ---
  @include mobile {
    --container-offset: 12px;
  }
}

// --------------------
// --- RESET STYLES ---
// --------------------

.title-reset {
  margin: 0;
}

.descr-reset {
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

.link-reset {
  color: inherit;
  text-decoration: none;
  outline: none;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.input-reset {
  padding: 0;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: transparent;
  outline: none;
}

// -----------------------
// --- SERVICE CLASSES ---
// -----------------------

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.hide {
  visibility: hidden;
  opacity: 0;
}

.img-style {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

// ---

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}
