.content-card {
  padding-top: 26px;
  padding-bottom: 50px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 12px;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    padding-bottom: 24px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 11px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 12px;
    padding-bottom: 43px;
  }
}

.content-card__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 33px 32px;

  // --- max-width: 1024px ---
  @include tablet {
    grid-template-columns: repeat(9, 1fr);
    gap: 23px 32px;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    gap: 0 0;
  }
}

// ------------------
// --- CARD IMAGE ---
// ------------------

.content-card__img-wrap {
  padding-top: 2px;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 1 / span 5;
    padding-top: 25px;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 1;
    grid-row: 1 / 2;
    padding: 3px 0 41px 0;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding: 3px 0 82px 0;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 0 0 14px 0;
  }
}

.content-card__img {

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 2px;
  }
}

// ------------------------------
// --- SAMPLES OF THE PRODUCT ---
// ------------------------------

.content-card__samples-wrap {
  display: flex;
  gap: 38px;
  padding: 10px 25px 9px 25px;
  background-color: $grey-background-color;

  // --- max-width: 1024px ---
  @include tablet {
    height: 81px;
    padding: 10px;
    overflow-x: auto;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    display: none;
  }
}

.content-card__samples-img {

  // --- max-width: 1024px ---
  @include tablet {
    max-width: none;
  }
}

// ----------------------------------
// --- CARD TITLE, PRICE, BUTTONS ---
// ----------------------------------

.content-card__headline {

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 6 / span 4;
    padding-top: 2px;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    grid-column: 1;
    grid-row: 2 / 3;
    padding: 0 0 39px 0;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding: 0 0 79px 0;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 0 0 28px 0;
  }
}

.headline__rating {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-bottom: 2px;
  color: #ff862f;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 3px;
  }
}

.headline__rating-icon {
  width: 16px;
  height: 15px;
  fill: #ff862f;
}

.headline {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.headline__title {
  margin-bottom: 4px;
  color: $text-color;
  font-size: 24px;
  font-weight: 400;
  line-height: 130%;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 7px;
    font-size: 16px;
  }
}

.headline__descr {
  margin-bottom: 22px;
  color: $grey-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;

  // --- max-width: 576px ---
  @include mobile {
    max-width: 230px;
    margin-bottom: 16px;
  }
}

.headline__price {
  margin-bottom: 21px;
  color: $text-color;
  font-size: 36px;
  font-weight: 400;
  line-height: 130%;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 16px;
    font-size: 24px;
  }
}

.headline__btn {
  margin-bottom: 10px;
  padding: 17px 34px;
  font-weight: 600;

  // --- max-width: 576px ---
  @include mobile {
    width: 100%;
    text-align: center;
  }
}

.headline__btn-wrap {

  // --- max-width: 576px ---
  @include mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
}

.headline__btn-add {
  padding: 3px;
  color: $primary-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;
  background-color: transparent;
  border-bottom: 1px solid transparent;
  transform: translateX(27px);
  transition: background-color 0.4s ease-in-out,
    color 0.4s ease-in-out,
    border-color 0.4s ease-in-out;

  // --- max-width: 576px ---
  @include mobile {
    transform: translateX(0);
  }
}

// --- product card add-button transitions ---

.headline__btn-add:focus-visible {
  color: $white-color;
  background-color: $primary-light-color;
}

@media (any-hover: hover) {
  .headline__btn-add:hover {
    border-color: $primary-color;
  }
}

.headline__btn-add:active {
  color: $primary-dark-color;
  border-color: $primary-dark-color;
}

