.feedback {
  padding-top: 34px;
  padding-bottom: 43px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-bottom: 41px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-bottom: 40px;
  }
}

.feedback__container {
  display: flex;
  justify-content: space-between;
  gap: 32px;

  // --- max-width: 576px ---
  @include mobile {
    gap: 0;
  }
}

// ------------------------
// --- BACKGROUND IMAGE ---
// ------------------------

.background::after {
  content: "";
  width: 624px;
  height: 383px;
  margin-top: 25px;
  background-image: url("../img/1920-feedback-img.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;

  // --- max-width: 1024px ---
  @include tablet {
    width: 514px;
    height: 316px;
    margin-top: 78px;
    background-image: url("../img/1024-feedback-img.png");
  }

  // --- max-width: 930px ---
  @include between-tablet {
    width: 187px;
    height: 444px;
    margin-top: 40px;
    margin-right: 40px;
    background-image: url("../img/768-feedback-img.png");
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-top: 12px;
    margin-right: 0;
  }

  // --- max-width: 576px ---
  @include mobile {
    // content: none;
    width: 0;
    height: 0;
    background-image: none;
  }
}

// --------------------------------
// --- FEEDBACK SECTION CONTENT ---
// --------------------------------

.feedback__content {
  flex-shrink: 0;

  // --- max-width: 768px ---
  @include small-tablet {
    flex-shrink: 1;
  }
}

.feedback__text-wrap {
  max-width: 624px;
  margin-bottom: 15px;

  // --- max-width: 1024px ---
  @include tablet {
    max-width: 405px;
  }
}

.feedback__title {
  @include title-section;
  margin-bottom: 10px;

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 9px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 8px;
  }
}

.feedback__descr {
  position: relative;
  color: $grey-dark-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 136%;
}

