.hero {
  display: flex;
  align-items: center;
  min-height: 400px;
  background-image: url("../img/1920-hero-banner.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  // --- max-width: 1440px ---
  @include big-desktop {
    background-image: url("../img/1024-hero-banner.png");
    background-position: center bottom;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    min-height: 440px;
    background-image: url("../img/768-hero-banner.png");
  }

  // --- max-width: 768px ---
  @include small-tablet {
    display: block;
    min-height: 400px;
  }

  // --- max-width: 576px ---
  @include mobile {
    min-height: 302px;
    background-image: unset;
  }
}

.hero__container {
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;

  // --- max-width: 1440px ---
  @include big-desktop {
    padding-top: 14px;
  }

  // --- max-width: 930px ---
  @include between-tablet {
    padding-bottom: 123px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 81px;
    padding-bottom: 15px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 30px;
  }
}

.hero__wrap {
  max-width: 496px;

  // --- max-width: 576px ---
  @include mobile {
    max-width: 100%;
    padding: 0px;
  }
}

.hero__title {
  margin-bottom: 46px;
  color: $grey-color;
  font-size: 48px;
  font-family: $font-family;
  font-weight: 700;
  line-height: 140%;

  // --- max-width: 1440px ---
  @include big-desktop {
    margin-bottom: 49px;
    line-height: 138%;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    font-size: 6vw;
    line-height: 142%;
    letter-spacing: 1px;
  }

  // --- max-width: 576px ---
  @include mobile {
    display: none;
  }
}

.hero__btn {

  // --- max-width: 576px ---
  @include mobile {
    width: 100%;
    text-align: center;
  }
}

// ---------------------------
// --- MOBILE SECTION ONLY ---
// ---------------------------

.hero__mobile-bg {
  display: none;

  // --- max-width: 576px ---
  @include mobile {
    position: relative;
    display: block;
    width: 102vw;
    top: -31px;
    left: 0%;
    z-index: -15;
    min-height: 66vw;
    margin-left: -4vw;
    margin-right: -50vw;
    background-image: url("../img/320-hero-banner.png");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
  }
}

.hero__title--mobile {

  // --- max-width: 576px ---
  @include mobile {
    display: block;
    max-width: 90vw;
    margin-bottom: 0;
    padding: 30px;
    font-size: 6vw;
    line-height: 145%;
    letter-spacing: 0.5px;
  }
}
