// ----------------------------
// --- FILTER SECTION TITLE ---
// ----------------------------

.catalog__filter-title {
  color: $text-color;
  font-size: 18px;
  font-weight: 400;
  line-height: 130%;

  // --- max-width: 1024px ---
  @include tablet {
    display: none;
  }
}

// -----------------------
// --- LIST OF FILTERS ---
// -----------------------

.filter {
  display: flex;
  flex-direction: column;
  gap: 25px;

  // --- max-width: 1024px ---
  @include tablet {
    flex-direction: row;
    gap: 45px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    flex-wrap: wrap;
  }

  // --- max-width: 576px ---
  @include mobile {
    gap: 15px 30px;
  }
}

// --------------------------
// --- ONE LIST OF FILTER ---
// --------------------------

.filter__sub-title {
  display: inline-block;
  margin-bottom: 8px;
  color: $grey-dark-color;
  font-size: 16px;
  line-height: 130%;
  font-weight: 600;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 10px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 9px;
  }
}

.filter__list {
  display: flex;
  flex-direction: column;
  gap: 9px;
}

// -----------------------
// --- FILTER CHECKBOX ---
// -----------------------

.filter__label {
  display: inline-block;
}

.filter__checkbox {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}

// -------------------
// --- FILTER TEXT ---
// -------------------

.filter__text {
  position: relative;
  display: flex;
  align-items: center;
  gap: 11px;
  color: $grey-dark-color;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
  transition: color 0.4s ease-in-out;
}

.filter__text::before {
  @include checkbox-square;
  width: 21px;
  height: 20px;
  transition: background-color 0.3s ease-in-out,
    border-color 0.3s ease-in-out,
    0.3s ease;
}

.filter__text::after {
  @include checkbox-checkmark;
  background-position: center 6px;
  width: 21px;
  height: 20px;
  transition: transform 0.4s ease-in-out;
}

// --- transitions of checkbox things and filter text ---

.filter__checkbox:focus-visible + .filter__text::before {
  outline: 1px solid $primary-color;
  outline-offset: 3px;
  }

@media (any-hover: hover) {
  .filter__text:hover {
    color: $primary-color;
  }
}

.filter__text:active {
  color: $primary-dark-color;
}

.filter__checkbox:checked + .filter__text::before {
  background-color: $primary-color;
  border-color: $primary-color;
  outline: none;
}

.filter__checkbox:checked + .filter__text::after {
  transform: scale(1);
}

// -----------------------
// --- RANGE OF PRICES ---
// -----------------------

.range {
  display: flex;
  flex-wrap: wrap;
  gap: 9px;
  padding-top: 2px;

  // --- max-width: 1024px ---
  @include tablet {
    gap: 10px;
    padding-top: 0;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 1px;
  }
}

.range__input {
  width: 86px;
  height: 30px;
  padding-left: 12px;
  border: 1px solid $grey-color;
  border-radius: 2px;
  transform: translateY(-5px);
  transition: border-color 0.4s ease-in-out;

  // --- max-width: 576px ---
  @include mobile {
    width: 85px;
  }
}

// --- range things transitions ---

.range__input:focus-visible {
  border-color: $primary-color;
}

@media (any-hover: hover) {
  .range__input:hover {
    border-color: $primary-light-color;
  }
}

.range__input:active {
  border-color: $primary-dark-color;
}

.range__input[type="number"] {
	-moz-appearance: textfield;
	-webkit-appearance: textfield;
	appearance: textfield;
}

.range__input[type="number"]::-webkit-outer-spin-button,
.range__input[type="number"]::-webkit-inner-spin-button {
	display: none;
}
