@mixin checkbox-checkmark {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 14px;
  height: 14px;
  transform: scale(0);
  background-image: url("../img/checkmark.svg");
  background-repeat: no-repeat;
  background-position: center 4px;
  background-size: 12px 9px;
  transition: transform 0.5s ease-in-out;
}
